import { graphql } from "gatsby";
import React from "react";
import { IQueryArrayResult, ICategory, IAuthor, IGatsbyLocation } from '../../interfaces';
import { ContentLayoutWithSidebar } from '../../components/Layout/ContentLayout';
import { SideBar } from "../../components/SideBar/SideBar";
import { SideBarQuickstart } from "../../components/SideBar/SideBarQuickstart";
import { SideBarHelp } from "../../components/SideBar/SideBarHelp";
import SideBarCategories from "../../components/SideBar/SideBarCategories";
import { IHeaderProps } from "../../components/Layout/Header";
import { SearchResults } from '../../components/Search/SearchResults';

interface IQueryResult
{
  data: {
    categories: IQueryArrayResult<ICategory>
    authors: IQueryArrayResult<IAuthor>
  };
  context: any;
  location: IGatsbyLocation;
}


export class Search extends React.Component<IQueryResult>
{
  constructor(props)
  {
    super(props);
  }
  render(){
    let header: IHeaderProps = {
      url: "search",
      description: `Sucherergebnisse`,
      type: "object",
      title: `Suchergebnisse`,
      tags: ["search"]
    }
    let query = "power";
    return (
      <ContentLayoutWithSidebar
      header={header} 
      sidebar={(
        <SideBar isFixed={true}>
          <SideBarQuickstart></SideBarQuickstart>
          <SideBarHelp></SideBarHelp>
          <SideBarCategories></SideBarCategories>
        </SideBar>
      )} 
      >
      <SearchResults data={this.props.data}></SearchResults>
      </ContentLayoutWithSidebar>
    )
  }
}

const Page = ({pageContext, data, location}) => {
  return (
    <Search data={data} location={location} context={pageContext}></Search>
  );
};
export default Page;

export const pageQuery = graphql`
{
    ...categories
    ...authorsMarkdown
}
`;
